import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import CssBaseline from '@material-ui/core/CssBaseline';
import Drawer from '@material-ui/core/Drawer';
import Hidden from '@material-ui/core/Hidden';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import { Toolbar, Avatar, Button } from '@material-ui/core';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Sidebar from './Sidebar';
import logo from '../Assets/images/mathcalcblack.png';
const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
    drawer: {
        [theme.breakpoints.up('md')]: {
            width: drawerWidth,
            flexShrink: 0,
        },
    },
    appBar: {
        backgroundColor: 'white',
        color: 'black !important',
        boxShadow: '0px 2px 3px -5px rgb(0 0 0 / 5%), 0px 0px 5px 0px rgb(0 0 0 / 4%), 0px 1px 10px 0px rgb(0 0 0 / 12%)',
        // [theme.breakpoints.up('sm')]: {
        //     width: `calc(100% - ${drawerWidth}px)`,
        //     marginLeft: drawerWidth,
        // },
    },
    menuButton: {
        color: 'black',
        [theme.breakpoints.up('md')]: {
            display: 'none',
        },
    },
    appTile: {
        color: 'black',
        [theme.breakpoints.down('sm')]: {
            display: 'none',
        },
    },
    // necessary for content to be below app bar

    drawerPaper: {
        width: drawerWidth,
        zIndex: 1,
        borderRight: 'none',
        backgroundColor: '#fafafa',
        boxShadow: '0px 2px 3px -5px rgb(0 0 0 / 5%), 0px 0px 5px 0px rgb(0 0 0 / 4%), 0px 1px 10px 0px rgb(0 0 0 / 12%)',
    },
    logo: {
        width: 35,
        height: 35,
        backgroundColor: 'white',
    },
    rightMenus: {
        position: 'absolute',
        right: '20px'
    },
    button: {
        height: 40,
        minWidth: "175px",
        background: '#997f7ff2 linear-gradient(77deg, #3e8df2 0%, #4a89d9 100%) 0% 0% no-repeat padding-box',
        borderRadius: '4px',
        color: 'white',
        // boxShadow: '1px 2px 5px 2px #afaead',
        [theme.breakpoints.down("xs")]: {
            minWidth: "146px",
            fontSize: "13px",
        },
        "&:hover": {
            color: '#fafafa',
            height: 43,
            width: '149px',
            boxShadow: '1px 3px 9px 2px #afaead',
        }
    }
}));

function Header(props) {
    const { window } = props;
    const classes = useStyles();
    const theme = useTheme();
    const [mobileOpen, setMobileOpen] = React.useState(false);

    const handleDrawerToggle = () => {
        setMobileOpen(!mobileOpen);
    };


    const container = window !== undefined ? () => window().document.body : undefined;

    return (
        <>
            <CssBaseline />
            <AppBar position="fixed" className={classes.appBar}>
                <Toolbar>
                    <IconButton
                        aria-label="open drawer"
                        edge="start"
                        onClick={handleDrawerToggle}
                        className={classes.menuButton}
                    >
                        <MenuIcon />
                    </IconButton>

                    <div style={{ display: 'flex', alignItems: 'center', margin: '10px 0px', cursor: 'pointer' }}
                        onClick={(e) => {
                            let anchor = document.createElement('a');
                            anchor.href = '/';
                            anchor.click();
                        }}>
                        <Avatar className={classes.logo}>
                            <img src={logo} alt="mathcalc logo" width={40} />
                        </Avatar>
                        <h2> <strong style={{
                            fontSize: '1.8rem',
                            marginLeft: '-10px'
                        }}>&nbsp;&nbsp; Mathcalc</strong></h2>
                    </div>
                    <div className={classes.rightMenus}>
                        <Button className={classes.button}
                            onClick={
                                () => {
                                    let anchor = document.createElement('a');
                                    anchor.href = '/feedback/';
                                    anchor.click();
                                }
                            }
                        >
                            Suggest a Tool
                    </Button>
                    </div>
                </Toolbar>
            </AppBar>
            <nav className={classes.drawer} aria-label="mailbox folders">
                {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
                <Hidden smUp implementation="css">
                    <Drawer
                        container={container}
                        variant="temporary"
                        anchor={theme.direction === 'rtl' ? 'right' : 'left'}
                        open={mobileOpen}
                        onClose={handleDrawerToggle}
                        classes={{
                            paper: classes.drawerPaper,
                        }}
                        ModalProps={{
                            keepMounted: true, // Better open performance on mobile.
                        }}
                    >
                        {<Sidebar handleDrawerToggle={handleDrawerToggle} />}
                    </Drawer>
                </Hidden>
                <Hidden smDown implementation="css">
                    <Drawer
                        classes={{
                            paper: classes.drawerPaper,
                        }}
                        variant="permanent"
                        open
                    >
                        {<Sidebar />}
                    </Drawer>
                </Hidden>
            </nav>
        </>
    );
}
export default Header;
